import React, { useEffect } from "react";
import { Root, addPrefetchExcludes } from "react-static";
import {
  fromUnixTime,
  getUnixTime,
  formatDuration,
  intervalToDuration,
} from "date-fns";
import "./app.css";
import {
  HttpResponse,
  SpotPriceJson,
  PriceHistoryJson,
  PriceEntity,
} from "types";
// import { throttle } from "lodash";

const BET_STARTED = fromUnixTime(1608177600);
const BET_ENDS = new Date("2021-01-17");
const BASE_URL = "https://min-api.cryptocompare.com/data";
const SPOT_URL = `${BASE_URL}/price?fsym=BTC&tsyms=USD`;
const HISTORY_URL =
  `${BASE_URL}/v2/histoday?fsym=BTC&tsym=USD&limit=50` +
  `&toTs=${getUnixTime(BET_ENDS)}`;

export async function http<T>(request: RequestInfo): Promise<HttpResponse<T>> {
  const response: HttpResponse<T> = await fetch(request, {
    mode: "cors",
    headers: {
      Accept: "text/plain",
    },
  });
  const text = await response.text();
  response.parsedBody = text ? JSON.parse(text) : {};
  return response;
}

var { format: inDollars } = new Intl.NumberFormat("en-US", {
  style: "currency",
  currency: "USD",
});

let started = false;

// Any routes that start with 'dynamic' will be treated as non-static routes
addPrefetchExcludes(["dynamic"]);

function App() {
  const [spotPrice, setSpotPrice]: [
    number,
    (setPrice: number) => void
  ] = React.useState<number>(0);

  const [priceHistory, setPriceHistory]: [
    PriceEntity[],
    (setPriceHistory: PriceEntity[]) => void
  ] = React.useState<PriceEntity[]>([]);

  const [loading, setLoading]: [
    boolean,
    (loading: boolean) => void
  ] = React.useState<boolean>(true);

  async function updatePrices() {
    console.log("updatePrices()", priceHistory.length);
    const [spotRes, histRes] = await Promise.all([
      http<SpotPriceJson>(SPOT_URL),
      http<PriceHistoryJson>(HISTORY_URL),
    ]);
    setSpotPrice(spotRes.parsedBody.USD);
    setPriceHistory(histRes.parsedBody.Data.Data);
    setLoading(false);
  }

  useEffect(() => {
    if (started) return;
    updatePrices();
    setInterval(() => updatePrices(), 10000);
    started = true;
  });

  function content() {
    const priceDelta = spotPrice - 22000;
    const [winner, loser] = priceDelta >= 0 ? ["son", "dad"] : ["dad", "son"];
    return (
      <div>
        <div>
          Bitcoin is currently worth{" "}
          <span
            className="emphasis"
            style={{ color: priceDelta >= 0 ? "#00FF00" : "#FF0000" }}
          >
            {inDollars(spotPrice)}
          </span>
          . It's {priceDelta >= 0 ? "up" : "down"}{" "}
          <span
            className="emphasis"
            style={{ color: priceDelta >= 0 ? "#00FF00" : "#FF0000" }}
          >
            {inDollars(Math.abs(priceDelta))}
          </span>{" "}
          since you made the bet{" "}
          {formatDuration(
            intervalToDuration({ start: BET_STARTED, end: new Date() })
          )}{" "}
          ago. The bet was for 10% of the difference in price, so:
          <div
            className="emphasis"
            style={{ padding: "20px", textAlign: "center", fontSize: "200%" }}
          >
            {loser} is down {inDollars(Math.abs(priceDelta * 0.1))} to {winner}
          </div>
        </div>
      </div>
    );
  }

  return (
    <Root>
      <div className="content">{loading ? "Loading..." : content()}</div>
    </Root>
  );
}

export default App;
